.App{
    width: 100%;
    height: 100vh;
    background-color: #1b1b32;
    font-family: lato, sans-serif;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.logo-contenedor{
    position: relative;
    width: 100%;
    height: 120px;
    margin: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.logo{
    position: relative;
    height: 100%;
}

.contenedor-calculadora{
    width: 400px;
    height: 600px;
    padding: 14px;
    background-color: #99c9ff;
    border-radius: 20px;
    border: 5px solid #fff;
}
.fila{
    margin: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
}